import React from 'react';
import './item-status-filter.css'

const ItemStatusFilter = ({filterItems, filterState }) =>  {

  const buttonsInfo = [
    {label: "All", id: 1},
    {label: "Active", id: 2},
    {label: "Done",  id: 3}
  ]

  const buttons = buttonsInfo.map(({ id, label }) => {
    const className = (filterState === label) ? 'btn-info' : 'btn-outline-secondary'

    return (
        <button key={id} type="button"
        className={`btn ` + className}
        onClick = {() => {filterItems(label)}}>{label}</button>
    )
  })

  return (
    <div className="btn-group">
      {buttons}
    </div>
  );
}

export default ItemStatusFilter;